}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.hero {
  background-color: #f46669;
  color: #FFFFFF;
  padding: 0.5rem 0;
  text-align: center;
  margin-bottom: 0;
}
@media (min-width: 992px) {
  .hero {
    text-align: left;
  }
}
.hero__heading {
  font-weight: 900;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .hero__heading {
    font-size: 3.2rem;
  }
}

.hero-blog {
  margin-bottom: 5rem;
}

.shadow {
  -webkit-box-shadow: 0px 0px 3px 0px rgb(240, 232, 240);
  -moz-box-shadow: 0px 0px 3px 0px rgb(240, 232, 240);
  box-shadow: 0px 0px 3px 0px rgb(240, 232, 240);
}

.shadow-picture {
  -webkit-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  padding: 3rem 0;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 992px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  max-width: 22rem;
  margin-bottom: 3rem;
}
@media (min-width: 992px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 992px) {
  .navegacion {
    flex-direction: row;
    gap: 3rem;
  }
}
.navegacion__enlace {
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.navegacion__enlace:hover {
  color: #f46669;
}
.navegacion__enlace:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 992px) {
  .navegacion__enlace {
    margin-bottom: 0;
  }
}

@media (min-width: 992px) {
  .experiencias__grid {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: dense;
  }
}

.bloque {
  height: 40rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.bloque:nth-child(1) {
  background-color: #f46669;
}
.bloque:nth-child(2) {
  background-image: url("../img/bloque_1.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.bloque:nth-child(3) {
  background-color: #c7c57d;
}
@media (min-width: 992px) {
  .bloque:nth-child(3) {
    grid-column: 2/3;
  }
}
.bloque:nth-child(4) {
  background-image: url("../img/bloque_2.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.bloque__contenido {
  padding: 1rem 0;
  border-block: 1px solid #FFFFFF;
  color: #FFFFFF;
  max-width: 40rem;
}
.bloque__heading {
  text-transform: uppercase;
  font-weight: 900;
}
.bloque__descripcion {
  font-size: 1.8rem;
}

.nosotros {
  padding: 5rem 0;
}
.nosotros__heading {
  text-align: center;
}
.nosotros__heading::after {
  content: "";
  display: block;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/separador.png);
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 992px) {
  .nosotros__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.nosotros__bloque {
  text-align: center;
  margin-bottom: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.nosotros__bloque:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 992px) {
  .nosotros__bloque {
    margin-bottom: 0;
  }
}
.nosotros__bloque-heading {
  font-weight: 400;
  margin-bottom: 0;
}
.nosotros__imagen {
  margin: 0 auto;
  width: 8rem;
}

.clases {
  background-color: #f5f5f5;
  padding: 5rem 0;
}
.clases__heading {
  text-align: center;
}
.clases__heading::after {
  content: "";
  display: block;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/separador.png);
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .clases__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .clases__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.clase {
  margin-bottom: 3rem;
  background-color: #FFFFFF;
}
.clase:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .clase {
    margin-bottom: 0;
  }
}
@media (min-width: 992px) {
  .clase:hover .clase__imagen {
    transform: scale(1.1);
  }
}
.clase__mask {
  overflow: hidden;
}
.clase__imagen {
  width: 100%;
  transform: scale(1);
  transition: all 2s;
}
.clase__meta {
  background-color: #f46669;
  color: #FFFFFF;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.clase__precio {
  font-size: 1.2rem;
  color: #f46669;
  font-weight: 700;
  background-color: #FFFFFF;
  border-radius: 0.5rem;
  padding: 0.4rem;
}
.clase__contenido {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.clase__nombre {
  font-weight: 400;
}
.clase__descripcion {
  color: #f46669;
  margin-top: 0;
}
.clase__enlace {
  background-color: #f46669;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.4s;
  text-align: center;
  padding: 1rem;
  display: block;
  font-size: 1.6rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .clase__enlace {
    width: auto;
  }
}
.clase__enlace:hover {
  background-color: #f1363a;
}

