.profesor {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
}
.profesor__picture {
  position: relative;
  overflow: hidden;
  height: 30rem;
}
.profesor__picture::before {
  background-color: #f46669;
  border-radius: 50%;
  bottom: 1.9rem;
  content: "";
  height: 20rem;
